@import url("https://fonts.googleapis.com/css2?family=Plus+Jakarta+Sans&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Unbounded:wght@600&display=swap");

.signup__body {
  min-height: 105vh;
  height: fit-content;
  width: 100vw;
  padding-bottom: 2rem;
  background: linear-gradient(
    250deg,
    #3dfff5,
    #4b0365
  );
  background-blend-mode: screen;
  display: flex;
  justify-content: center;
  margin-top: -10rem;
}

.signup__box {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: rgb(45, 45, 45);
    font-family: "Plus Jakarta Sans";
}

.signup__outerBox {
  background-color: white;
  border-radius: 1rem;
  margin-top: 12rem;
  width: 480px;
  height: fit-content;
  padding: 2.5rem;
  
}

.signup__form {
    text-align: center;
    margin-top: 1rem;
}

.signup__back {
  text-decoration: none;
  color: rgb(30, 30, 30);
  font-family: "Unbounded";
  font-weight: bolder;
  font-size: 0.7rem;
}

.signup__apparelwala {
  color: rgb(31, 31, 31);
  font-size: 1.5rem;
  margin: 1rem 0rem 1rem 0rem;
  font-family: "Unbounded";
}

.signup__heading {
  color: rgb(30, 30, 30);
  font-size: 2rem;
  font-family: "Plus Jakarta Sans";
}

.signup__email {
  background-color: rgba(226, 236, 255, 1);
  color: rgb(49, 49, 49);
  width: 480px;
  border: none;
  height: 56px;
  margin-bottom: 1rem;
}

.signup__password {
  background-color: rgba(226, 236, 255, 1);
  color: rgb(49, 49, 49);
  width: 480px;
  border: none;
  height: 56px;
  margin-bottom: 1rem;
}

.signup__btn {
  display: block;
  width: 480px;
  height: 56px;
  border-radius: 8px;
  cursor: pointer;
  margin-bottom: 1rem;
}

.btn_google {
  border: 1px solid rgb(46, 46, 46);
  color: rgb(46, 46, 46);
  margin-top: 1rem;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn_facebook {
  border: none;
  background-color: rgba(24, 119, 242, 1);
  color: white;
  font-weight: 900;
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn_signup {
  background-color: #4136f1;
  border: none;
}


