  .chooseType {
    background: linear-gradient(
    250deg,
    #3dfff5,
    #4b0365
  );
  background-blend-mode: screen;
    min-height: 100vh;
    padding-top: 2rem;
    margin-top: -10rem;
    padding-bottom: 3rem;
  }
  
  .type__heading {
    text-align: center;
    font-size: 2.5rem;
    font-weight: bolder;
    padding: 2rem;
    margin-top: 6rem;
  }
  
  .businesstype__desc {
    text-align: center;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    margin-bottom: 2rem;
  }
  
  .businesstype__body {
    display: flex;
    justify-content: center;
  }
  
  .wrapper_type {
    display: flex;
    width: fit-content;
  }
  
  .typeBusiness {
    width: 22rem;
    height: 22rem;
    background: white;
    margin: 0.5rem;
    border-radius: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .typeBusiness__heading {
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 38px;
    color: #2b2539;
    text-align: center;
    margin-top: 1rem;
  }
  
  .typeBusiness__desc {
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  text-align: center;
  color: #2b2539;
  padding: 0rem 2rem 0rem 2rem;
  margin-top: 1rem;
  }
  
  .business__name {
    color: #2b2539;
  }
  
  .businesstype_desc {
    color: #2b2539;
  }
  
